import { createStore } from "vuex";

export default createStore({
  state: {
    cart: {
      items: [],
    },
    isAuthenticated: false,
    token: "",
    isLoading: false,
  },
  mutations: {
    INITIALIZE_STORE(state) {
      if (localStorage.getItem("cart")) {
        state.cart = JSON.parse(localStorage.getItem("cart"));
      } else {
        localStorage.setItem("cart", JSON.stringify(state.cart));
      }

      if (localStorage.getItem("token")) {
        state.token = localStorage.getItem("token");
        state.isAuthenticated = true;
      } else {
        state.toke = "";
        state.isAuthenticated = false;
      }
    },
    ADD_TO_CART(state, item) {
      //item l mt object gm c product (object) v quantiy
      //Kim tra tn ti product trong cart bng filter. Nu trong cart m c product.id trng vi item.product.id
      const exists = state.cart.items.filter(
        (i) => i.product.id === item.product.id
      );
      //Nu exists.length > 0
      if (exists.length) {
        //Quantity ca card tng thm s item.quantity
        exists[0].quantity =
          parseInt(exists[0].quantity) + parseInt(item.quantity);
        console.log(exists[0].quantity, item.quantity);
      } else {
        // Nu exists.length = 0 tc l cha c item
        state.cart.items.push(item);
      }
      localStorage.setItem("cart", JSON.stringify(state.cart));
    },
    SET_ISLOADING(state, status) {
      state.loading = status;
    },
    SET_TOKEN(state, token) {
      state.token = token;
      state.isAuthenticated = true;
    },
    REMOVE_TOKEN(state, token) {
      state.token = "";
      state.isAuthenticated = false;
    },
    CLEAR_CART(state) {
      state.cart = { items: [] };
      localStorage.setItem("cart", JSON.stringify(state.cart));
    },
  },
  actions: {},
  modules: {},
});
